import { I_HotspotConfigStyle } from '@containers/Home/types';
import React, { memo } from 'react';
import Lottie from 'react-lottie';
import { LottieIconWrapper } from './styled';

interface Props {
  lottie?: I_HotspotConfigStyle['lottie'];
}

const LottieIcon = ({ lottie }: Props) => {
  return (
    <LottieIconWrapper>
      <Lottie
        options={{
          loop: lottie?.loop || false,
          autoplay: true,
          path: lottie?.json_src,
          rendererSettings: {
            preserveAspectRatio: 'xMidYMid slice',
          },
        }}
      />
    </LottieIconWrapper>
  );
};

export default memo(LottieIcon);
